        p.mt-3 Lo primero a tener en cuenta es que los patrones se trabajan por cuartos de medida si la prenda es simtrica para que sea ms prctico el desarrollo de la moldera. El estndar de elongacin que se trabaja para un panti en talla M es de 90 % a 150 %. Para el caso de industrializar la moldera se aplica un porcentaje de descuento del 125 % y se descuenta en una cadera estndar talla M, de 96 cm de contorno; la siguiente es manera de la aplicacin de la frmula:
