              p.mb-0.ps-4 96 cm/4 = (96 cm contorno de cadera dividido en el nmero de piezas del patrn para realizar el descuento individual) = 24 cm.
              p.ps-4 24 (resultado del porcentaje) se divide en 4 (nmero de piezas del patrn) = 6 cm.
